import React, { useState, useRef } from "react";
import {
  Search,
  List,
  Cell,
  Group,
  Header,
  Avatar,
  Button,
} from "@vkontakte/vkui";


import Icon24Filter from "@vkontakte/icons/dist/24/filter";
import Icon24MoreHorizontal from "@vkontakte/icons/dist/24/more_horizontal";

const addsArr = [
  { id: 3201, name: "Объявление1" },
  { id: 3273, name: "Объявление2" },
  { id: 3205, name: "Объявление3" },
  { id: 3282, name: "Объявление4" },
  { id: 3283, name: "Объявление5" },
  { id: 3284, name: "Объявление6" },
  { id: 3285, name: "Объявление7" }
];

const AddsTab = props => {
  const [search, setSearch] = useState("");

  function onChange(e) {
    setSearch(e.target.value);
  }

  function adds() {
    const s = search.toLowerCase();
    return addsArr.filter(({ name }) => name.toLowerCase().indexOf(s) > -1);
  }

  return (
    <div>
      <Search
        value={search}
        onChange={onChange}
        icon={<Icon24Filter />}
        onIconClick={props.onFiltersClick}
      />
      <List>
        {adds().map(thematic => (
          <Cell key={thematic.id}>{thematic.name}</Cell>
        ))}
      </List>
      <Group header={<Header mode="secondary">Большая ячейка</Header>}>
              <List>
                <Cell
                  before={<Avatar size={72} />}
                  size="l"
                  description="Друзья в Facebook"
                  asideContent={<Icon24MoreHorizontal />}
                  bottomContent={
                    <div style={{ display: 'flex' }}>
                      <Button size="m">Добавить</Button>
                      <Button size="m" mode="secondary" style={{ marginLeft: 8 }}>Скрыть</Button>
                    </div>
                  }
                >
                  Семён Ефимов</Cell>
                <Cell
                  before={<Avatar size={72} />}
                  size="l"
                  description="29 лет, Санкт-Петербург"
                  asideContent={<Icon24MoreHorizontal />}
                  bottomContent={
                    <div style={{ display: 'flex' }}>
                      <Button size="m">Добавить</Button>
                      <Button size="m" style={{ marginLeft: 8 }}>Скрыть</Button>
                    </div>
                  }
                >
                  Александр Попов</Cell>
                <Cell
                  before={<Avatar size={72} />}
                  size="l"
                  description="Команда ВКонтакте"
                  asideContent={<Icon24MoreHorizontal />}
                  bottomContent={
                    <div style={{ display: 'flex' }}>
                      <Button size="m">Добавить</Button>
                      <Button size="m" mode="secondary" style={{ marginLeft: 8 }}>Скрыть</Button>
                    </div>
                  }
                >
                  Екатерина Скобейко</Cell>
              </List>
            </Group>
    </div>
  );
};

export default AddsTab;
